.admin-checkbox {
  /* Base styles */
  appearance: none; /* Remove default browser styling */
  cursor: pointer;
  width: 18px;
  height: 18px;
  border: 1px solid var(--app-color-primary);
  border-radius: 0.25rem; /* Corresponds to rounded */
  background-color: var(--app-color-light);
  vertical-align: middle; /* Helps align with text */
  position: relative;
  display: inline-block; /* Ensure it takes space */
  flex-shrink: 0; /* Prevent shrinking in flex containers if needed */

  /* Accent color for the checkmark */
  accent-color: var(--app-color-primary);

  /* Ring styles (focus) */
  outline: none; /* Remove default outline */
  box-shadow: none; /* Equivalent to ring-0 */
  transition: box-shadow 0.15s ease-in-out, background-color 0.15s ease-in-out,
    border-color 0.15s ease-in-out;
}

/* Focus styles */
.admin-checkbox:focus {
  /* Equivalent to focus:ring-1 focus:ring-offset-0 focus:ring-primary */
  box-shadow: 0 0 0 1px var(--app-color-primary);
}

/* Checked styles */
.admin-checkbox:checked {
  background-color: var(--app-color-primary);
  border-color: var(--app-color-primary);
  /* Add a checkmark using SVG background */
  background-image: url("data:image/svg+xml,%3csvg viewBox='0 0 16 16' fill='white' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M12.207 4.793a1 1 0 010 1.414l-5 5a1 1 0 01-1.414 0l-2-2a1 1 0 011.414-1.414L6.5 9.086l4.293-4.293a1 1 0 011.414 0z'/%3e%3c/svg%3e");
  background-size: 100% 100%;
  background-position: center;
  background-repeat: no-repeat;
}

/* Dark mode styles (assuming you have a .dark class on a parent, like <html> or <body>) */
.dark .admin-checkbox {
  border-color: var(--admin-zinc-600);
  background-color: var(--admin-zinc-700);
}

.dark .admin-checkbox:checked {
  /* Keep primary colors for checked state even in dark mode */
  background-color: var(--app-color-primary);
  border-color: var(--app-color-primary);
}
